import React from "react";
import Flip from 'react-reveal/Flip';

function Footer() {
    const year = new Date().getFullYear();
    return (
        <footer class="border-top " style={{ backgroundColor: "white", opacity: 0.95 }}>
            <Flip left>
                <div class="container px-4 px-lg-5">
                    <div class="row gx-4 gx-lg-5 justify-content-center">
                        <div class="col-md-10 col-lg-8 col-xl-7">
                            <ul class="list-inline text-center">
                                <li class="list-inline-item">
                                    <a href="https://www.linkedin.com/in/%E6%A1%90%E6%85%88-%E5%BC%B5-583170244/">
                                        <span class="fa-stack fa-2xl">
                                            <i class="fas fa-circle fa-stack-2x"></i>
                                            <i class="fab fa-linkedin fa-stack-1x fa-inverse"></i>
                                        </span>
                                    </a>
                                </li>
                                <li class="list-inline-item">
                                    <a href="https://www.facebook.com/people/%E5%BC%A0%E6%A1%90%E6%85%88/pfbid02H4tov1XExJ4y2oZzoubFCS32ydWy5V6TdvBKbmDd9EJUxzTyBv9B25DDF2toYTsml/">
                                        <span class="fa-stack fa-2xl">
                                            <i class="fas fa-circle fa-stack-2x"></i>
                                            <i class="fab fa-facebook-f fa-stack-1x fa-inverse"></i>
                                        </span>
                                    </a>
                                </li>
                                <li class="list-inline-item">
                                    <a href="https://github.com/ZTongci">
                                        <span class="fa-stack fa-2xl">
                                            <i class="fas fa-circle fa-stack-2x"></i>
                                            <i class="fab fa-github fa-stack-1x fa-inverse"></i>
                                        </span>
                                    </a>
                                </li>
                            </ul>
                            <div class="small text-center text-muted fst-italic"><p>Copyright &copy; Tongci {year} </p></div>
                        </div>
                    </div>
                </div>
            </Flip>
        </footer>
    );
}

export default Footer;