import React from "react";

import Zoom from 'react-reveal/Zoom';


function MessageCotent(props){
    return (
        
              <div class="content" >
             <Zoom>
            <p>{props.content}</p>
            <button className="btn btn-outline-dark"><a href={props.buttonLink} className="more-button">FIND OUT MORE</a></button>
            </Zoom>
            </div>
            
    );
}

export default MessageCotent;