import { click } from "@testing-library/user-event/dist/click";
import React from "react";
import { Link } from "react-router-dom";
import meLogo from "../image/me-logo.png";
import Tada from 'react-reveal/Tada';

function Navbar(){
  const  [Menu, setMenu] = React.useState("home");
  
  function chaneColor(clickMenu){
    setMenu(clickMenu);
    }

    return (
        <nav class="navbar bg-light fixed-top nav-style" style={{opacity:"0.95"}}>
  <div class="container-fluid">
  <Tada>
    <Link class="navbar-brand" to="/welcome/"><img src={meLogo} alt="" srcset="" class="me-logo"/></Link>
    </Tada>
    <button class="navbar-toggler" type="button" data-bs-toggle="offcanvas" data-bs-target="#offcanvasNavbar" aria-controls="offcanvasNavbar">
      <span class="navbar-toggler-icon"></span>
    </button>
    <div class="offcanvas offcanvas-end" tabindex="-1" id="offcanvasNavbar" aria-labelledby="offcanvasNavbarLabel" style={{fontSize:"40px"}}>
      <div class="offcanvas-header">
        <h5 class="offcanvas-title nav-style" id="offcanvasNavbarLabel" style={{fontSize:"40px"}}>Welcome</h5>
        <button type="button" class="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
      </div>
      <div class="offcanvas-body">
        <ul class="navbar-nav justify-content-end flex-grow-1 pe-3">
          <li class="nav-item">
          <span style={{fontSize:"40px"}} data-bs-toggle="offcanvas" data-bs-target=".navbar-offcanvas.show"><Link  class="nav-link" aria-current="page" to="/welcome/" onMouseOver={()=>{chaneColor("home")}} onClick={()=>{chaneColor("home")}} style={Menu=="home"?{color:"white",backgroundColor:"rgb(50,50,46)"}:{color:"#0000008C"}}>Home</Link ></span>
          </li>
          <li class="nav-item">
          <span style={{fontSize:"40px"}} data-bs-toggle="offcanvas" data-bs-target=".navbar-offcanvas.show"><Link  class="nav-link" to="/welcome/aboutme" onMouseOver={()=>{chaneColor("about")}}  onClick={()=>{chaneColor("about")}} style={Menu=="about"?{color:"white",backgroundColor:"rgb(50,50,46)"}:{color:"#0000008C"}}>About Me </Link></span>
          </li>
          <li class="nav-item">
          <span style={{fontSize:"40px"}} data-bs-toggle="offcanvas" data-bs-target=".navbar-offcanvas.show"><Link class="nav-link" to="/welcome/contact" onMouseOver={()=>{chaneColor("contact")}} onClick={()=>{chaneColor("contact")}} style={Menu=="contact"?{color:"white",backgroundColor:"rgb(50,50,46)"}:{color:"#0000008C"}}>Contact</Link></span>
          </li>
          <li class="nav-item dropdown">
            <a  class="nav-link dropdown-toggle"  role="button" data-bs-toggle="dropdown" aria-expanded="false" onMouseOver={()=>{chaneColor("work")}} onClick={()=>{chaneColor("work")}} style={(Menu=="work"||Menu=="blog"||Menu=="message"||Menu=="product")?{color:"white",backgroundColor:"rgb(50,50,46)",fontSize:"40px"}:{color:"#0000008C",fontSize:"40px"}}>
              Work
            </a>
            <ul class="dropdown-menu nav-style">
            <span data-bs-toggle="offcanvas" data-bs-target=".navbar-offcanvas.show"><li><Link class="dropdown-item" to="/welcome/products" onMouseOver={()=>{chaneColor("product")}} onClick={()=>{chaneColor("product")}} style={Menu=="product"?{color:"white",backgroundColor:"rgb(50,50,46)",fontSize:"30px"}:{color:"#0000008C",fontSize:"30px"}}>Products</Link></li></span>
            <span data-bs-toggle="offcanvas" data-bs-target=".navbar-offcanvas.show"><li><Link class="dropdown-item" to="/welcome/messages" onMouseOver={()=>{chaneColor("message")}} onClick={()=>{chaneColor("message")}} style={Menu=="message"?{color:"white",backgroundColor:"rgb(50,50,46)",fontSize:"30px"}:{color:"#0000008C",fontSize:"30px"}}>Message</Link></li></span>
              <li>
                <hr class="dropdown-divider"/>
              </li>
              <span  data-bs-toggle="offcanvas" data-bs-target=".navbar-offcanvas.show"><li><Link class="dropdown-item" to="/welcome/blog" onMouseOver={()=>{chaneColor("blog")}} onClick={()=>{chaneColor("blog")}} style={Menu=="blog"?{color:"white",backgroundColor:"rgb(50,50,46)",fontSize:"30px"}:{color:"#0000008C",fontSize:"30px"}}>Blog</Link></li></span>
            </ul>
          </li>
        </ul>
      </div>
    </div>
  </div>
</nav>
   );
}

export default Navbar;