import React from "react";
import Navbar from "./Navbar";
import Footer from "./Footer";
import DynamicImage from "./DynamicImage";
import Home from "../pages/Home";
import Contact from "../pages/Contact";
import AboutMe from "../pages/AboutMe";
import Products from "../pages/Products";
import Message from "../pages/Message";
import Blog from "../pages/Blog";


import { BrowserRouter, Routes, Route } from "react-router-dom";



function App() {
  


    return (<div>
        <Navbar />
        <DynamicImage />
        <Routes>
        <Route path={`/`} element={<Home />} />
        <Route path={`/welcome`} element={<Home />} />
        <Route path={`/welcome/contact`} element={<Contact />} />
        <Route path={`/welcome/aboutme`} element={<AboutMe />} />
        <Route path={`/welcome/products`} element={<Products />} />
        <Route path={`/welcome/messages`} element={<Message/>} />
        <Route path={`/welcome/blog`} element={<Blog />} />
      </Routes>
    

        <Footer />
    </div>
    );
}

export default App;